import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Route, Redirect } from 'react-router-dom';
import { fetchCurrentUser } from './actions/auth';
import PrivateRouter from './components/common/PrivateRoute';
import GuestRoute from './components/common/GuestRoute';
import RootContainer from './containers/RootContainer';
import HomePage from './components/pages/HomePage';
import LoginPage from './components/pages/LoginPage';
import ReportPage from './components/pages/ReportPage';

class App extends React.Component {
  componentWillMount() {
    if (localStorage.VelaEnvatoToken) {
      this.props.fetchCurrentUser(localStorage.VelaEnvatoToken);
    }
  }

  render() {
    const { location } = this.props;
    return (
      <div className="app-container">
        <h1>What the fuck</h1>
        <Route
          location={location}
          path='https://duchv90.github.io/ReactEnvatoAPI/login'
          exact
          component={LoginPage}
        />
        <Route
          location={location}
          path='https://duchv90.github.io/ReactEnvatoAPI/'
          exact
          component={HomePage}
        />
      </div>
    );
  }
}

App.propTypes = {
  fetchCurrentUser: PropTypes.func.isRequired,
  location: PropTypes.shape({
    pathname: PropTypes.string.isRequired
  }).isRequired
};

export default connect(null, { fetchCurrentUser })(App);



